import React from 'react';
import { Typography, makeStyles } from '@material-ui/core';
import { Timeline, TimelineItem, TimelineSeparator, TimelineDot, TimelineConnector, TimelineOppositeContent, TimelineContent } from '@material-ui/lab'
import ProjectPreview from './ProjectPreview';

const useStyles = makeStyles({
    opposite_content:
    {
        flex: 0.5,
    },
});

export default function ProjectTimeline(props: any) {

    const classes = useStyles();

    const yearContent = function (year: string) {
        const rows: JSX.Element[] = [];
        const projects = Object.keys(props.timelineData[year]);
        const projectsData = props.timelineData[year];
        let i = 0;
        projects.forEach(project => {
            i++;
            rows.push(<ProjectPreview key={i} showProject={props.showProject} projectPath={props.projectType+"/"+year+"/"+project} projectName={project} previewImage={projectsData[project].PreviewImage}></ProjectPreview>);            
        });
        return rows;
    }

    const icon = function(i : number)
    {
        if (props.timelineIcon !== null && props.timelineIcon !== undefined)
            return <TimelineDot>{props.timelineIcon}</TimelineDot>;
        else 
            return <TimelineDot></TimelineDot>;
    }

    const years = function () {
        const rows: JSX.Element[] = [];
        if (props.timelineData) {
            const years = Object.keys(props.timelineData);
            for (let i = 0; i < years.length; i++) {
                rows.push(
                    <TimelineItem key={i}>
                        <TimelineOppositeContent className={classes.opposite_content}>
                            <Typography variant="body2" color="textSecondary">
                                {years[i]}
                            </Typography>
                        </TimelineOppositeContent>
                        <TimelineSeparator>
                            {icon(i)}
                            <TimelineConnector />
                        </TimelineSeparator>
                        <TimelineContent>
                            {yearContent(years[i])}
                        </TimelineContent>
                    </TimelineItem >
                );
            }
        }
        return rows;
    }

    return (
        <Timeline align="left">
            {years()}
        </Timeline >
    );
}



