import React, { useEffect } from 'react';
import axios from 'axios'
import root from 'react-shadow';

export default function AnimationIcon(props: { src: string; className: string | undefined; }) {
    const [animation, setAnimation] = React.useState("");

    useEffect(() => {
        if (props.src.endsWith(".svg"))
        {
            const fetchData = async () => {
                const axiosInstance = axios.create({ baseURL: process.env.PUBLIC_URL });
                axiosInstance.get(props.src)
                    .then(async function (response: { [x: string]: any }) {
                        if (response.data !== "")
                            setAnimation(response.data);
                    })
                    .catch(function (error: any) {
                        // handle error
                        console.log(error);
                    })
                    .finally(function () {
                        // always executed
                    });
            };
            fetchData();
        }        
    }, [props.src]);

    return (
        <div className={props.className}>
        <root.div>
        <div className={props.className} dangerouslySetInnerHTML={{__html: animation}}></div>
        </root.div>
        </div> 
    );
}



